n {\n\n  &:focus {\n    outline: 3px solid $brand-green !important;\n  }\n}\n\n  // margin-top: -41px !important;\n  // margin-left: -12.5px !important;\n  // width: 25px !important;\n  // height: 41px !important;\n  //\n  // &.large {\n  //   width: 50px !important;\n  //   height: 82px !important;\n  //   margin-top: -82px !important;\n  //   margin-left: -25px !important;\n  // }\n\n.marker-cluster,\n.marker-cluster div {\n  border-radius: 500px;\n}\n\n.marker-cluster.active {\n  outline: 2px solid yellow;\n}\n\n.marker-cluster div {\n  height: 30px;\n  width: 30px;\n  margin: 5px;\n  text-align: center;\n  line-height: 30px;\n}\n\n.my-marker-cluster-small div,\n.my-marker-cluster-medium div,\n.my-marker-cluster-large div {\n  background-color: white;\n}\n\n.my-marker-cluster-small {\n  background-color: lighten($brand-green,5%);\n  box-shadow: 9px 9px 12px -7px rgba(0,0,0,0.25) !important;\n}\n\n.my-marker-cluster-medium {\n  background-color: $brand-green;\n  box-shadow: 9px 9px 12px -7px rgba(0,0,0,0.25) !important;\n}\n\n.my-marker-cluster-large {\n  background-color: darken($brand-green,10%);\n  box-shadow: 9px 9px 12px -7px rgba(0,0,0,0.25) !important;\n}\n\n\n\n// Popup\n// .leaflet-container a.leaflet-popup-close-button {\n\n// }\n\n.leaflet-pane {\n  z-index: 1001;\n\n  a.leaflet-popup-close-button {\n    top: ms(-2) !important;\n    right: ms(-2) !important;\n    width: 1em !important;\n    height: 1em !important;\n    font: 25px/12px Tahoma, Verdana, sans-serif !important;\n    border-radius: 99999px;\n    padding: 4px;\n    background-color: darken($brand-green, 20%);\n    color: black;\n\n    @media screen and (orientation: landscape) and (min-width: 800px) {\n      top: ms(0) !important;\n      right: ms(0) !important;\n      width: 1em !important;\n      height: 1em !important;\n    }\n  }\n}\n\n.leaflet-popup {\n  width: 90vw;\n  // max-width: 95%;\n  @media screen and (orientation: landscape) and (min-width: 800px) {\n    width: 550px;\n  }\n}\n\n.leaflet-popup-content-wrapper {\n    overflow: hidden;\n    border: 1px solid $medium-gray;\n    border-bottom: none;\n    position: relative;\n    font-size: 1rem !important;\n    padding: 0;\n    // border-radius: 0 !important;\n    box-shadow: 13px 19px 12px -7px rgba(0,0,0,0.2) !important;\n\n    .leaflet-popup-content {\n      margin: 0 !important;\n      width: auto !important;\n    }\n\n    .entry-title {\n      text-transform: capitalize;\n      font-size: ms(1);\n      font-weight: 400;\n      margin-bottom: 0;\n\n      @media screen and (orientation: landscape) and (min-width: 800px) {\n        font-size: ms(2);\n        margin-bottom: ms(-1);\n      }\n    }\n\n    .entry-category {\n      background-color: $brand-light-green;\n      padding: 0 10px;\n      font-size: ms(-1);\n      border-radius: 99999px;\n      display: inline-block;\n    }\n\n    .entry-address {\n      font-size: ms(-1);\n      color: $gray;\n      text-transform: capitalize;\n      padding-left: 1em;\n\n      &::before {\n        content: '';\n        margin-left: -1.25em;\n        display: inline-block;\n        background-image: url(/assets/images/marker-icon-red.svg);\n        width: 1em;\n        height: 1.5em;\n        vertical-align: bottom;\n        margin-right: .25em;\n        background-size: contain;\n        background-repeat: no-repeat;\n        background-position: left;\n      }\n    }\n\n    p {\n      margin: 0 0 ms(-4);\n\n      @media screen and (orientation: landscape) and (min-width: 800px) {\n        margin: 0 0 ms(0);\n      }\n    }\n\n    .entry-links {\n      display: flex;\n      flex-direction: row;\n\n      > * + * {\n        margin-left: ms(-6);\n      }\n    }\n\n    .entry-link {\n      margin-bottom: ms(0);\n\n      a {\n        display: inline-block;\n        color: $dark-gray;\n        background-color: $medium-gray;\n        border-radius: 99999px;\n        line-height: ms(4);\n        padding: 0 ms(-2);\n      }\n\n    }\n\n    .entry-social {\n